@import 'colors';
/* --- BOUTONS --- */
.btn{
    background-color: $CG_WhiteIvoire;
    color: $CG_RougePrincipal;
    border-radius: 25px;
    border: 0px;
    padding: 5px;
    width: 120px;
    font-weight: 600;
    font-size: 15px;
    transition: all .2s ease-in;
}
.btn:hover{
    transform: scale(1.1);
}
.btnInverse{
    appearance: button;
    text-decoration: none;
    display:block;
    background-color: $CG_RougePrincipal;
    color: $CG_WhiteIvoire;
    border-radius: 25px;
    border: 0px;
    padding: 5px 15px;
    width: auto;
    font-weight: 600;
    transition: all .2s ease-in;
}
.btnInverse:hover{
    transform: scale(1.1);
    text-decoration: none;
}
.btn-more {
    margin: 0 auto;
    margin-top: 15px;
}
/* --- Séparateurs --- */
.separateur-inToRed {
    height: 30px;
    width: 100%;
    background-color: $CG_WhiteIvoire;
    color: green;
    border-radius: 0px 0px 30px 30px;
}
.separateur-outToWhite {
    height: 30px;
    width: 100%;
    background-color: $CG_WhiteIvoire;
    border-radius: 30px 30px 0px 0px;
    margin-top: 20px;
}
